


































import { Component, Vue } from 'vue-property-decorator';
import Nav from '@/components/common/Nav.vue'
import Footer from '@/components/common/Footer.vue'
import TopicListItem from "@/components/topic/TopicListItem.vue";
import TopicPopup from "@/components/topic/TopicPopup.vue";

@Component({
  components: {
    Nav,
    Footer,
    TopicListItem,
    TopicPopup
  },
})

export default class Topic extends Vue {
  private pageTitle?: String = '全部专题';
  private topicList?: any[] = [];
  private showPopup?: boolean = false;
  private loading?: boolean = false;
  private finished?: boolean = false;
  private refreshing?: boolean = false;
  private loadData(): void {
    setTimeout(() => {
      if (this.refreshing) {
        this.topicList = [];
        this.refreshing = false;
      }
      this.topicList = [
        { activity: '黄驴推荐', date: '中美日韩海纳百川，国产自拍照样新鲜，黄驴倾情力荐，部部精选，久经考验，老司机身经百战的进阶之路！……', img: 'bg1' },
        { activity: '成人动漫', date: '萝莉人妻美御姐，正太痴汉老色批，你和你的二次元老婆们。', img: 'bg2' },
        { activity: '制服诱惑', date: '护士服、水手服、兔女郎、JK制服、女仆装、女教师……真的有人不喜欢穿制服的小姐姐们吗？', img: 'bg3' },
        // { activity: '元旦礼金大放送', date: '2020.12.15-2021.1.15', img: 'giftMoney' },
        // { activity: '元旦礼金大放送', date: '2020.12.15-2021.1.15', img: 'giftMoney' }
      ];
      this.loading = false;
      this.finished = true;
    }, 1000);
  }
  private closePopup(v: boolean) {
    this.showPopup = v;
  }
  private onRefresh(): void {
    this.finished = false;
    this.loading = false;
    this.loadData();
  }
}
